* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

.country-list {
  list-style: none;
}

.country-list-item {
  display: flex;
  align-items: center;
  gap: 10px;
  font-size: 22px;
  height: 50px;
}

.country-info {
  font-size: 22px;
}

.country-name-wrap {
  display: flex;
  align-items: center;
  gap: 10px;
}

.country-capital,
.country-population,
.country-languages {
  font-weight: bold;
}
